import React from "react";
import clsx from "clsx";
import { makeStyles, useTheme } from "@material-ui/core/styles";

import {
  Drawer,
  Divider,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import List from "@material-ui/core/List";
import InfoIcon from "@material-ui/icons/Info";
import AppsIcon from "@material-ui/icons/Apps";
import ComputerIcon from "@material-ui/icons/Computer";
import MoneyIcon from "@material-ui/icons/Money";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import TwitterIcon from "@material-ui/icons/Twitter";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import PhoneIcon from "@material-ui/icons/Phone";
import MailIcon from "@material-ui/icons/Mail";
import ShopIcon from "@material-ui/icons/Shop";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1,
    },
  },
}));

export default function TemporaryDrawer({
  drawerOpen,
  onClose,
  onChangeContent,
  focus,
}) {
  const theme = useTheme();
  const classes = useStyles();

  return (
    <div onClick={onClose} onKeyDown={onClose} role="presentation">
      <Drawer
        open={drawerOpen}
        onClose={onClose}
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: drawerOpen,
          [classes.drawerClose]: !drawerOpen,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: drawerOpen,
            [classes.drawerClose]: !drawerOpen,
          }),
        }}
      >
        <IconButton onClick={onClose}>
          {theme.direction === "rtl" ? (
            <ChevronRightIcon />
          ) : (
            <ChevronLeftIcon />
          )}
        </IconButton>

        <Divider />
        <List>
          <ListItem
            button
            autoFocus={true}
            selected={focus.about}
            divider
            onClick={() => onChangeContent("about")}
          >
            <ListItemIcon>
              <InfoIcon />
            </ListItemIcon>
            <ListItemText primary={"About Me"} />
          </ListItem>

          <ListItem
            button
            selected={focus.projects}
            divider
            onClick={() => onChangeContent("projects")}
          >
            <ListItemIcon>
              <AppsIcon />
            </ListItemIcon>
            <ListItemText primary={"Projects"} />
          </ListItem>

          <ListItem
            button
            selected={focus.competitions}
            divider
            onClick={() => onChangeContent("competitions")}
          >
            <ListItemIcon>
              <ComputerIcon />
            </ListItemIcon>
            <ListItemText primary={"Competitions"} />
          </ListItem>

          <ListItem
            button
            selected={focus.awards}
            divider
            onClick={() => onChangeContent("awards")}
          >
            <ListItemIcon>
              <MoneyIcon />
            </ListItemIcon>
            <ListItemText primary={"Awards"} />
          </ListItem>
        </List>

        <ListItem
          button
          selected={focus.mail}
          divider
          onClick={() => onChangeContent("mail")}
        >
          <ListItemIcon>
            <MailIcon />
          </ListItemIcon>
          <ListItemText primary={"Talk To Me"} secondary={"Drop me an email"} />
        </ListItem>

        <Divider />

        <List>
          <ListItem
            button
            divider
            onClick={() => {
              window.open("https://github.com/v1b3m/", "_blank").focus();
            }}
          >
            <ListItemIcon>
              <GitHubIcon />
            </ListItemIcon>
            <ListItemText primary={"GitHub"} secondary={"@v1b3m"} />
          </ListItem>

          <ListItem
            button
            divider
            onClick={() => {
              window
                .open(
                  "https://play.google.com/store/apps/developer?id=v1b3m",
                  "_blank"
                )
                .focus();
            }}
          >
            <ListItemIcon>
              <ShopIcon />
            </ListItemIcon>
            <ListItemText
              primary={"Google Play Store"}
              secondary={"Developer account"}
            />
          </ListItem>

          <ListItem
            button
            divider
            onClick={() => {
              window
                .open(
                  "https://www.linkedin.com/in/benjamin-mayanja-2a2028137/",
                  "_blank"
                )
                .focus();
            }}
          >
            <ListItemIcon>
              <LinkedInIcon />
            </ListItemIcon>
            <ListItemText primary={"LinkedIn"} />
          </ListItem>

          <ListItem
            button
            divider
            onClick={() => {
              window.open("https://twitter.com/BenjaMayanja", "_blank").focus();
            }}
          >
            <ListItemIcon>
              <TwitterIcon />
            </ListItemIcon>
            <ListItemText primary={"Twitter"} secondary={"@BenjaMayanja"} />
          </ListItem>

          <ListItem button divider>
            <ListItemIcon>
              <PhoneIcon />
            </ListItemIcon>
            <ListItemText primary={"Call Me"} secondary={"+256783258401"} />
          </ListItem>
        </List>
        <Divider />
      </Drawer>
    </div>
  );
}
