import React, { useState } from "react";
import { withSnackbar } from "notistack";
import { makeStyles } from "@material-ui/core/styles";
import {
  Input,
  InputLabel,
  InputAdornment,
  FormControl,
  TextField,
  Button,
  FormHelperText,
} from "@material-ui/core";
import { AccountCircle, Email } from "@material-ui/icons";
import unirest from "unirest";

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },
}));

function ContactForm({ enqueueSnackbar, setLoading }) {
  const classes = useStyles();
  const [message, updateMessage] = useState("");
  const [email, updateEmail] = useState("");
  const [name, updateName] = useState("");
  const [validEmail, setValidEmail] = useState(true);

  const validateEmail = () => {
    var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    setValidEmail(re.test(String(email).toLowerCase()));
  };

  const sendEmail = async () => {
    setLoading(true);
    const response = await unirest(
      "POST",
      "https://gmailclient256.herokuapp.com/feedback/v1b3m"
    )
      .headers({
        Accept: "application/json",
        "Content-Type": "application/json",
      })
      .send({ email, name, message })
      .then((res) => res);

    if (response.error) {
      enqueueSnackbar("An error has occurred");
      if (response.error.raw_body.message) {
        enqueueSnackbar(response.error.raw_body.message);
      }
    } else {
      enqueueSnackbar(response.raw_body.message);
      enqueueSnackbar(`I'll be in touch shortly`);
      enqueueSnackbar(`Thanks ${name}`);
    }

    setLoading(false);
    updateName("");
    updateEmail("");
    updateMessage("");
  };

  return (
    <div>
      <FormControl className={classes.margin}>
        <InputLabel htmlFor="input-with-icon-adornment">Your name</InputLabel>
        <Input
          id="input-with-icon-adornment"
          startAdornment={
            <InputAdornment position="start">
              <AccountCircle />
            </InputAdornment>
          }
          onChange={(event) => {
            updateName(event.target.value);
          }}
          value={name}
        />
      </FormControl>
      <FormControl className={classes.margin}>
        <InputLabel htmlFor="input-with-icon-adornment">Your email</InputLabel>
        <Input
          id="input-with-icon-adornment"
          startAdornment={
            <InputAdornment position="start">
              <Email />
            </InputAdornment>
          }
          error={!validEmail}
          onChange={(event) => {
            updateEmail(event.target.value);
            validateEmail(email);
          }}
          value={email}
        />
        <FormHelperText id="my-helper-text">
          We'll never share your email.
        </FormHelperText>
      </FormControl>
      <TextField
        id="standard-textarea"
        label="Your message"
        placeholder="No need to be formal, just type in what's on your mind"
        multiline
        rows={4}
        style={{ marginBottom: 10 }}
        onChange={(event) => {
          updateMessage(event.target.value);
        }}
        value={message}
      />
      <br />
      <Button variant="contained" onClick={sendEmail}>
        Send
      </Button>
    </div>
  );
}

export default withSnackbar(ContactForm);
