import React, { useState } from "react";
import { SnackbarProvider } from "notistack";
import "./App.css";

import Header from "./components/Header";
import MiniDrawer from "./components/drawers/MiniDrawer";
import Content from "./components/Content";
import TemporaryDrawer from "./components/drawers/TemporaryDrawer";

import { CssBaseline, makeStyles } from "@material-ui/core";

const useStyles = makeStyles(() => ({
  root: {
    display: "flex",
  },
}));

function App() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [contentType, setContentType] = useState("about");
  const [loading, setLoading] = useState(false);

  const [focus, changeFocus] = useState({
    about: true,
    projects: false,
    competitions: false,
    awards: false,
    mail: false,
  });

  const handleLoading = (value) => {
    setLoading(value);
  };

  const handleCloseMenu = () => {
    setOpen(false);
  };

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const getWindowDimensions = () => {
    const { innerWidth: width, innerHeight: height } = window;
    return {
      width,
      height,
    };
  };

  const handleChangeContent = (type) => {
    setContentType(type);
    const notFocused = {
      about: false,
      projects: false,
      competitions: false,
      awards: false,
      mail: false,
    };
    changeFocus({ ...notFocused, [type]: true });
  };

  return (
    <SnackbarProvider maxSnack={3}>
      <div className={classes.root}>
        <CssBaseline />
        <Header
          drawerOpen={open}
          onOpen={handleDrawerOpen}
          width={getWindowDimensions().width}
        />

        {getWindowDimensions().width > 480 && (
          <MiniDrawer
            drawerOpen={open}
            onClose={handleCloseMenu}
            onChangeContent={handleChangeContent}
            focus={focus}
          />
        )}
        {getWindowDimensions().width <= 480 && (
          <TemporaryDrawer
            drawerOpen={open}
            onClose={handleCloseMenu}
            onChangeContent={handleChangeContent}
            focus={focus}
          />
        )}
        <div>
          <Content
            type={contentType}
            loading={loading}
            setLoading={handleLoading}
            onChangeContent={handleChangeContent}
          />
        </div>
      </div>
    </SnackbarProvider>
  );
}

export default App;
